.App {
  text-align: center;
  margin-left: 1%;
  margin-right: 1%;
  margin-top: 3%;
  background-color: white;
}

body {
  background: #A0C5E8
}

.letters {
  background-color: blueviolet;
  padding: 5px;
  margin: 1px;
  font-size: xx-large;
}

h1 {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.rules-list {
  text-align: left;
}

html,
body {
  margin: 0;
  padding: 0;
}

body {
  background: #aaaaaa;
  font-family: 'Roboto', sans-serif;
}

.container {
  display: grid;
  grid-gap: 10px;
  grid-template-areas:
    "aside"
    "main"
  ;
  width: 100%;
  margin: 0 auto;
}

@media (min-width: 500px) {
  .container {
    grid-template-columns: 30% 1fr;
    grid-template-areas:
      "aside main"
    ;
    width: 992px;

  }
}

main {
  grid-area: main;
  background-color: white;
  padding: 1%;
  align-items: center;
}

aside {
  grid-area: aside;
  background-color: cadetblue;
  padding: 1%
}

aside .inner,
main .inner {
  background: rgb(138, 138, 138);
  padding: 10px;
  border-radius: 25px;
}